import Vue from 'vue'
import Router from 'vue-router'
import PageContent from '@/components/PageContent'
import PageContent1 from '@/components/PageContent1'
import PageContent2 from '@/components/PageContent2'
import PageContent3 from '@/components/PageContent3'
import ResourceLoading from '@/components/ResourceLoading'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'ResourceLoading',
      component: ResourceLoading
    },
    {
      path: '/PageContent',
      name: 'PageContent',
      component: PageContent
    },
    {
      path: '/PageContent1/:id?',
      name: 'PageContent1',
      component: PageContent1
    },
    {
      path: '/PageContent2',
      name: 'PageContent2',
      component: PageContent2
    },
    {
      path: '/PageContent3',
      name: 'PageContent3',
      component: PageContent3
    }
  ]
})



