<template>
  <!--可以通过url传参数-->
  <div class="container" :style="{height:pageHeight+'px'}">
    <div class="page page-current">
      <div ref="car" class="car" style="position:absolute;">
        <img src="@/assets/untitled.png">
      </div>
      <img ref="bg" :src="bgImage" class="bg">
      <svg
        width="100%"
        :height="pageHeight"
        xmlns="http://www.w3.org/2000/svg"
        xmlns:xlink="http://www.w3.org/1999/xlink"
      >
        <!-- Created with Method Draw - http://github.com/duopixel/Method-Draw/ -->
        <g>
          <path
            ref="svg_4"
            d="m346.5,397c0,-2 58,24 58,24c0,0 73,66 73,66c0,0 40,47 47,56c7,9 38,70 37.5,70c0.5,0 16.5,50 16.5,50c0,0 8,74 7.5,74c-5.5,66 -18.5,112 -19,112c-29.5,78 -72.5,136 -72.5,136c-134,164 -209,218 -209,218c-26,22 -139,122 -139.5,122c-53.5,78 -69.5,121 -70,121c-15.5,84 -5.5,113 -5.5,113c4,45 30,100 30,100c7,26 124,150 124,150c98,66 190,114 190,114c0,0 37,2 100,86c63,84 90,212 90,212c12,62 14,212 14,212c-13,124 -60,262 -60,262c-58,211 -167,408 -167,408c-66,144 -179,292 -179,292c-90,84 -134,236 -134,236c-22,74 -18,169 -18,169c-2,116 64,247 64,247c74,178 294,368 294,368c0,0 126,98 132,244c-12,136 -112,235 -112,235c-20,22 -226,178 -226,178c-122,103 -144,209 -144,209c-10,58 0,130 0,130c41,128 110,195 110,195c0,0 178,146 177.5,146"
            opacity="0.5"
            fill-opacity="null"
            stroke-opacity="null"
            stroke-width="0"
            fill="none"
          ></path>
        </g>
      </svg>
      <div
        class="duihua"
        ref="duihuacontainer"
        v-for="(img,index) in imgs"
        :key="index+'-'+id"
        :style="{top:(img.startY+offsetY)+'px',left:img.left+'px'}"
      >
        <img :src="img.path" :alt="index" ref="duihua" :style="{opacity:(img.opacity||0)}">
      </div>
      <!-- <img class="duihua" :src="img.path" v-for="img in imgs" :key="img.id" :style="{top:img.top+'px',left:img.left+'px',opacity:img.opacity}"  /> -->

      <div class="bottom" v-if="nextPageUrl!=null">
        <button @click="gotoClick">下一页</button>
      </div>
      <transition name="fade">
        <div class="tip" v-if="showTip">
          <img src="@/assets/AjOMa3pH.gif">
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import PageConfig from "@/assets/js/PageConfig.js";
export default {
  data() {
    return {
      showContainer: true,
      pageConfig: PageConfig,
      id: 0
    };
  },
  computed: {
    pageHeight() {
      return this.pageConfig[this.id].pageHeight;
    },
    showY() {
      return this.pageConfig[this.id].showY || window.innerHeight / 2;
    },
    offsetY() {
      return this.pageConfig[this.id].offsetY || 100; //从开始位置偏移距离
    },
    speed() {
      return this.pageConfig[this.id].speed || 20; //滚轮转动一格移动速度
    },
    showTip() {
      return this.pageConfig[this.id].showTip;
    },
    nextPageUrl() {
      return this.pageConfig[this.id].nextPageUrl;
    },
    bgImage() {
      return this.pageConfig[this.id].bgImage;
    },
    imgs() {
      return this.pageConfig[this.id].imgs;
    }
  },
  watch: {
    $route(to, from) {
      //console.log(this.getStatus(this.$route.path))
    }
  },
  methods: {
    gotoClick() {
      this.$router.push(this.nextPageUrl);
    }
  },
  mounted() {
    let hideTip = setTimeout(() => {
      this.pageConfig[this.id].showTip = false;
    }, 5000);

    $(window).on("scroll", () => {
      this.pageConfig[this.id].showTip = false;
      if (hideTip) {
        clearTimeout(hideTip);
      }
    });

    this.id = this.$route.params.id || 0;

    window.scrollTo(0, 0);
    var path = this.$refs["svg_4"];
    var $car = $(this.$refs["car"]);

    var timer = null;
    // 最重要的两个属性  获取长度，以及每个点的坐标
    var totalLength = path.getTotalLength();
    var initPosition = path.getPointAtLength(0);
    var lastPosition = path.getPointAtLength(totalLength);
    var prePosition = initPosition;
    var curPosition = initPosition;

    // 获取前进切线角度
    function getRotate(a, b) {
      var k = (b.y - a.y) / (b.x - a.x);
      var rotate = (Math.atan(k) * 180) / Math.PI;
      return k < 0 ? rotate + 90 : rotate - 90;
    }

    // 定位car
    $car
      .css({
        left: initPosition.x,
        top: initPosition.y
        //'-webkit-transform': 'translate(-2px, 0) rotate(' + getRotate(curPosition, path.getPointAtLength(1)) + 'deg)'
      })
      .show();

    var t = this;

    window.requestAnimationFrame =
      window.requestAnimationFrame ||
      function(fn) {
        setTimeout(() => {
          fn();
        }, 10);
      };

    function render() {
      let scrollY =
        window.pageYOffset ||
        window.scrollY ||
        document.documentElement.scrollTop;
      //
      curPosition = path.getPointAtLength(
        (totalLength / (lastPosition.y - initPosition.y)) * scrollY
      );
      $car
        .css({
          left: curPosition.x,
          top: curPosition.y
          //'-webkit-transform': 'translate(-2px, 0) rotate(' + getRotate(prePosition, curPosition) + 'deg)'
        })
        .show();

      //$car.css({"-webkit-transform":'translate('+curPosition.x+'px,'+ curPosition.y+'px)'}).show()
      prePosition = curPosition;

      t.showGotoButton = t.pageHeight - scrollY <= 1500;
      if (t.imgs.length) {
        for (var i = 0; i < t.$refs["duihuacontainer"].length; i++) {
          let container = t.$refs["duihuacontainer"][i];
          let img = t.$refs["duihua"][i];
          // let imgData=t.imgs[i]
          let containerRect = container.getBoundingClientRect();
          // console.log(containerRect)
          let top = parseFloat($(img).css("top"));
          let opacity = parseFloat($(img).css("opacity"));
          if (containerRect.top <= t.showY) {
            top -= t.speed;
            opacity += t.speed / t.offsetY;
            if (top < -t.offsetY) {
              top = -t.offsetY;
            }
            if (opacity > 1) {
              opacity = 1;
            }
          } else {
            top += t.speed;
            opacity -= t.speed / t.offsetY;
            if (top > 0) {
              top = 0;
            }
            if (opacity < 0) {
              opacity = 0;
            }
          }

          $(img).css({ top: top + "px", opacity: opacity });
        }
      }

      requestAnimationFrame(render);
    }
    render();

    //  let debounce
    $(window).on("scroll", function(e) {
      //render()
    });
  }
};
</script>


<style scoped>
.bg {
  position: absolute;
  top: 0;
  width: 100%;
}
path {
  stroke-dasharray: 3000;
  stroke-dashoffset: 3000;
  -webkit-animation: dash 5s linear infinite;
  animation: dash 5s linear infinite;
}

.container {
  position: absolute;
  width: 100%;
  overflow: hidden;
}

.container .page .duihua {
  position: absolute;
  width: 100%;
}
.container .page .duihua img {
  position: relative;
}

.car {
  z-index: 999;
}
.car img {
  position: relative;
  left: -20px;
  top: -40px;
}
.bottom {
  width: 100%;
  position: absolute;
  text-align: center;
  bottom: 100px;
}
.bottom button {
  font-size: 0.5rem;
}
.tip {
  position: fixed;
  width: 100%;
  bottom: 5px;
  text-align: center;
}
</style>



