var imgs = [
  require("@/assets/1-0.jpg"),
  require("@/assets/2-0.jpg"),
  require("@/assets/3-0.jpg"),
]

// 第一页需要的图片
for (var i = 1; i <= 7; i++){
  imgs.push(require("@/assets/1-"+i+".png"))
}

// 第二页需要的图片
for (var i = 1; i <= 7; i++) {
  imgs.push(require("@/assets/2-" + i + ".png"))
}

// 第三页需要的图片
for (var i = 1; i <= 7; i++) {
  imgs.push(require("@/assets/3-" + i + ".png"))
}
module.exports = {
  imgs
}



