  <div class="container" :style="{height:heigh+'px'}">
    <div class="page page-current">
      <div ref="car" class="car" style="position:absolute;width:20px;height:20px;">
        <img src="@/assets/untitled.png" />
      </DIV>
      <svg width="640" :height="heigh" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
        <!-- Created with Method Draw - http://github.com/duopixel/Method-Draw/ -->

        <g>
          <title>background</title>
          <rect fill="#fff" id="canvas_background" :height="heigh" width="642" y="-1" x="-1" />
          <g display="none" id="canvasGrid">
            <rect fill="url(#gridpattern)" stroke-width="0" y="0" x="0" height="100%" width="100%" id="svg_2" />
          </g>
        </g>
        <g>
          <title>Layer 1</title>
          <image xlink:href="@/assets/1-0.jpg" id="svg_1" :height="heigh" width="640" y="0" x="0" />
          <path ref="svg_4"
            d="m346.5,397c0,-2 58,24 58,24c0,0 73,66 73,66c0,0 40,47 47,56c7,9 38,70 37.5,70c0.5,0 16.5,50 16.5,50c0,0 8,74 7.5,74c-5.5,66 -18.5,112 -19,112c-29.5,78 -72.5,136 -72.5,136c-134,164 -209,218 -209,218c-26,22 -139,122 -139.5,122c-53.5,78 -69.5,121 -70,121c-15.5,84 -5.5,113 -5.5,113c4,45 30,100 30,100c7,26 124,150 124,150c98,66 190,114 190,114c0,0 37,2 100,86c63,84 90,212 90,212c12,62 14,212 14,212c-13,124 -60,262 -60,262c-58,211 -167,408 -167,408c-66,144 -179,292 -179,292c-90,84 -134,236 -134,236c-22,74 -18,169 -18,169c-2,116 64,247 64,247c74,178 294,368 294,368c0,0 126,98 132,244c-12,136 -112,235 -112,235c-20,22 -226,178 -226,178c-122,103 -144,209 -144,209c-10,58 0,130 0,130c41,128 110,195 110,195c0,0 178,146 177.5,146"
            opacity="0.5" fill-opacity="null" stroke-opacity="null" stroke-width="0"  fill="none" />
        </g>
      </svg>
      <img class="duihua" :src="img.path" v-for="img in imgs" :key="img.id" :style="{top:img.top+'px',left:img.left+'px',opacity:img.opacity}"  />

      <!-- <div class="bottom" v-if="showGotoButton">
      <button  @click="gotoClick">下一页</button>
      </div> -->
      <!-- <transition name="fade">
      <div class="tip" v-if="showTip">
        <img src="@/assets/AjOMa3pH.gif" />
      </div>
      </transition> -->
    </div>
  </div>

</template>

<script>
export default {
  data(){
return {
  heigh:6946,
  carTop:400,
  marginTop:400,
  showTip:true,
  showGotoButton:false,
  imgs:[
      {
        id:1,
        path:require("@/assets/1-1.png"),
        originTop:850,
        top:850,
        maxTop:575,
        left:150,
        opacity:0,
        startY:400,//滚动位置需要减去400
        endY:500
      },
      {
        id:2,
        path:require("@/assets/1-2.png"),
        originTop:1600,//初始位置
        maxTop:1400,//最终位置
         left:130,
          opacity:0,
          startY:1025,
          endY:1200
      },
      {
        id:3,
        path:require("@/assets/1-3.png"),
        originTop:2350,//初始位置
        maxTop:2100,//最终位置
         left:180,
          opacity:0,
          startY:1750,
          endY:1925
      },
      {
        id:4,
        path:require("@/assets/1-4.png"),
        originTop:2650,//初始位置
        maxTop:2500,//最终位置
         left:130,
          opacity:0,
          startY:2425,// 执行动画开始位置
          endY:2500// 执行动画结束位置
      },
      {
        id:5,
        path:require("@/assets/1-5.png"),
        originTop:3150,//初始位置
        maxTop:2900,//最终位置
         left:130,
          opacity:0,
          startY:2700,// 执行动画开始位置
          endY:2900// 执行动画结束位置
      },
      {
        id:6,
        path:require("@/assets/1-6.png"),
        maxTop:3600,//最终位置
        originTop:3900,//起点位置
         left:130,
          opacity:0,
          startY:3200,// 执行动画开始位置
          endY:3400// 执行动画结束位置
      },
        {
        id:7,
        path:require("@/assets/1-7.png"),
        originTop:4850,//初始位置
        maxTop:4600,//最终位置
         left:190,
          opacity:0,
          startY:4050,// 执行动画开始位置
          endY:4400// 执行动画结束位置
      },
        {
        id:8,
        path:require("@/assets/1-7.png"),
        originTop:5500,//初始位置
        maxTop:5300,//最终位置
         left:130,
          opacity:0,
          startY:4975,// 执行动画开始位置
          endY:5075// 执行动画结束位置
      }
    ]
}
  },
  methods:{
      gotoClick(){
        this.$router.push({name:'PageContent3'})
      }
    
  },
  mounted(){
    setTimeout(()=>{
      this.showTip=false
    },5000)

for(let i=0;i< this.imgs.length;i++){
        let img=this.imgs[i]
        img.startY-=this.marginTop
        img.endY-=this.marginTop
        img.top=img.originTop
        }

window.scrollTo(0,0)
  var path = this.$refs['svg_4'];
  var $car = $(this.$refs['car']);

  var timer = null;
  // 最重要的两个属性  获取长度，以及每个点的坐标
  var totalLength = path.getTotalLength();
  var initPosition = path.getPointAtLength(0);
  var prePosition = initPosition;
  var curPosition = initPosition;

  // 获取前进切线角度
  function getRotate(a, b) {
    var k = (b.y - a.y) / (b.x - a.x);
    var rotate = Math.atan(k) * 180 / Math.PI;
    return k < 0 ? rotate + 90 : rotate - 90;
  }

  // 定位car 
  $car.css({
    left: initPosition.x,
    top: initPosition.y,
    //'-webkit-transform': 'translate(-2px, 0) rotate(' + getRotate(curPosition, path.getPointAtLength(1)) + 'deg)'
  }).show()

 
 var t=this

 window.requestAnimationFrame=window.requestAnimationFrame||function(fn){
   setTimeout(()=>{
     fn()
   },10)
 }

 function render(){
   let scrollY=window.pageYOffset||window.scrollY||document.documentElement.scrollTop
      curPosition = path.getPointAtLength(totalLength / (t.heigh-window.innerHeight-t.carTop) *scrollY );

      $car.css({
        left: curPosition.x,
        top: curPosition.y,
        //'-webkit-transform': 'translate(-2px, 0) rotate(' + getRotate(prePosition, curPosition) + 'deg)'
      }).show();
 
      //$car.css({"-webkit-transform":'translate('+curPosition.x+'px,'+ curPosition.y+'px)'}).show()
      prePosition = curPosition;

      t.showGotoButton=(t.heigh-scrollY)<=1500;
      // 
      for(let i=0;i< t.imgs.length;i++){
        let img=t.imgs[i]
        
        if( scrollY<=img.endY){
          img.top=img.originTop- (scrollY-img.startY)/(img.endY-img.startY)*(img.originTop-img.maxTop)
          img.opacity=(scrollY-img.startY)/(img.endY-img.startY)
        } else{
          img.top=img.maxTop
          img.opacity=1
        }
      
      }

      requestAnimationFrame(render)
 }
 render()

//  let debounce
// $(window).on("scroll", function (e) {
//     if(debounce){
// 			clearTimeout(debounce);
// 		}
// 		debounce = setTimeout(function(){
// 			render()
// 		},0);


// })
//   $(window).on("scroll", function (e) {
//       let scrollY=window.pageYOffset||window.scrollY||document.documentElement.scrollTop
//       curPosition = path.getPointAtLength(totalLength / (t.heigh-window.innerHeight-t.carTop) *scrollY );

//       $car.css({
//         left: curPosition.x,
//         top: curPosition.y,
//         //'-webkit-transform': 'translate(-2px, 0) rotate(' + getRotate(prePosition, curPosition) + 'deg)'
//       }).show();
//       prePosition = curPosition;

        
// t.showGotoButton=(t.heigh-scrollY)<=1500;
//       // 
//       for(let i=0;i< t.imgs.length;i++){
//         let img=t.imgs[i]
        
//         if( scrollY<=img.endY){
//           img.top=img.originTop- (scrollY-img.startY)/(img.endY-img.startY)*(img.originTop-img.maxTop)
//           img.opacity=(scrollY-img.startY)/(img.endY-img.startY)
//         } else{
//           img.top=img.maxTop
//           img.opacity=1
//         }
//         console.log(img.top)
//       }
//   })
  }
}
</script>


<style scoped>
 path {
    stroke-dasharray: 3000;
    stroke-dashoffset: 3000;
    -webkit-animation: dash 5s linear infinite;
    animation: dash 5s linear infinite;
  }

  .container {
    position: absolute;
    width: 100%;
    overflow: hidden;
  }

  .container .page .duihua {
     position: absolute;

  }

  .car{
        z-index: 999;
  }
  .car img{
      position: relative;
    left: -20px;
    top: -40px;
  }
  .bottom{
    width:100%;
    position: absolute;
    text-align: center;
    bottom: 100px;
  }
  .bottom button{
    font-size:0.5rem;
  }
  .tip{
    position: fixed;
    width:100%;
    bottom:5px;
    text-align: center
  }

</style>



